*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.drum-machine{
  display: flex;
  flex-direction: column;
  padding: 50px;
  background-color: grey;
}
#display{
  width: 100%;
  height: 50px;
  font-size: 25px;
  background-color: black;
  color: white;
  margin-bottom: 20px;
  padding-top: 10px;
  text-align: center;
}
#keys-container{
  display: flex;
  flex-wrap: wrap;
  width: 170px;
  justify-content: space-evenly;
}
.drum-pad{
  display: flex;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 20px;
  background-color: #026100;
  color: #FFF199;
  margin-bottom: 10px;
  cursor: pointer;
}